import { shuffleArray } from './common.js'

export function getAutoChat(input, name) {
	// stop words
	input = removeStopWords(input)
	let candidates = []
	for (let i = 0; i < input.length - 1; i++)
		for (let item of autoChatList) {
			const word = input[i] + input[i + 1]
			if ((item.match && item.match.search(word) >= 0) || (item.two && item.two.search(word) >= 0) || (item.one && item.one.search(input[i]) >= 0))
				candidates.push(item)
		}
	candidates = candidates.filter(cand => {
		if (Math.random() > 1 / parseInt(localStorage['autoChatCount' + cand.idx]))
			return false
		localStorage['autoChatCount' + cand.idx] = parseInt(localStorage['autoChatCount' + cand.idx]) + 1
		return true
	})
	if (!candidates.length)
		return
	return candidates[Math.floor(Math.random() * candidates.length)].msg
}
export function randAutoChat() {
	return autoChatList[Math.floor(Math.random() * autoChatList.length)].msg
}
function removeStopWords(input) {
	return input.replace(/\d+/g, '').replace(/[\[\]\.]/g, '').replace(/[]/g, '').replace(/[]/, '')
}

